.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}

.slwc-field-disable-css {
  opacity: .5 !important;
  pointer-events: none !important;
  cursor: default !important;
}

.slwc-text {
  border-bottom: solid 1px;
  margin: 10px 0;
  padding: 0 0 10px;
  font-size: 14px;
  align-items: center;
  display: flex;

  svg {
    width: 18px;
    height: auto;
    margin-right: 6px;
  }
}

.slwc-text + select.slwc-custom {
  display: none !important;
}

table.shop_table {

  .product-name {

    > a {
      display: block;
    }

    label.slw_cart_item_stock_location_label {
      margin-right: 10px;
    }

    select.slw_item_stock_location {
      display: inline !important;
    }
  }
}

body.single-product {

  .stock.in-stock {
    display: none;
  }
}
 
.slwc-hidden {
  display: none !important;
}

.product-unavailable-tag {
  font-size: 16px;
  font-weight: normal;
  width: 100%;
  position: absolute;
  color: #fff;
  line-height: normal;
  padding: 2px 4px;
  background: rgba(1,1,1,.5);
  border-radius: 1px;
  margin-bottom: 4px;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 9;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: underline;
}